<template>
<body>
  <h2 class="title">Course Information Discoverer</h2>
    <div class="group">
      <div id="select">
        <div class="uiLabel">University State</div>
        <select class="uidropdownstate" v-model="uniState1" id="university-states">
         <option v-for="option in universityStates" :key="option.id" v-bind:value="option">
            {{ option }}
          </option>
        </select>
      </div>
      
      <div id="select">
        <div class="uiLabel">University State</div>
        <select class="uidropdownstate" v-model="uniState2" id="university-states">
         <option v-for="option in universityStates" :key="option.id" v-bind:value="option">
           {{ option }}
         </option>
       </select>
      </div>
    </div>
    
    <div class="group">
      <div id="select">
        <div class="uiLabel">University 1</div>
        <select class="uidropdownuni" id="universities" v-model="university1">
         <option v-for="option in filteredUnis1" :key="option.id" v-bind:value="option">
           {{ option }}
         </option>
       </select>  
      </div>
      
      <div id="select">
        <div class="uiLabel">University 2</div>
        <select class="uidropdownuni" id="universities" v-model="university2">
         <option v-for="option in filteredUnis2" :key="option.id" v-bind:value="option">
           {{ option }}
         </option>
       </select>  
      </div>
    </div>
    
    <div class="group">
      <div id="select">
       <div class="uiLabel">Course</div>
       <select class="uidropdowncourse" id="courses" v-model="course1" @change="onCourseChange1()">
         <option v-for="option in filteredCourses1" :key="option.id" v-bind:value="option.url">
           {{ option.title }}
         </option>
       </select>
      </div>
      
     <div id="select">
       <div class="uiLabel">Course</div>
       <select class="uidropdowncourse" id="courses" v-model="course2" @change="onCourseChange2()">
         <option v-for="option in filteredCourses2" :key="option.id" v-bind:value="option.url">
           {{ option.title }}
         </option>
       </select>
      </div>
    </div>

    <div class = "charts">
    <h2>Line Chart</h2>
   <GChart
      type="LineChart"
      :options="options"
      :data="collectionData"
    />

    <h2>Fee Comparison</h2>
    <GChart
      type="ColumnChart"
      :options="columnOptions"
      :data="chartData"
    />
    </div> 

    <div class="descriptionGroup">
      <div id="description">
       <div class="uiLabel" v-for="option in filteredCourseUrl1" :key="option.url"><a v-bind:href="option.url"> {{ option.university}} <br> {{ option.title }}</a> <br> Description:</div>
       <p v-for="option in filteredCourseUrl1" :key="option.id" > {{ option.description }}</p>
      </div>
      
     <div id="description">
       <div class="uiLabel" v-for="option in filteredCourseUrl2" :key="option.url"><a v-bind:href="option.url"> {{ option.university}} <br> {{ option.title }}</a> <br> Description:</div>
       <p v-for="option in filteredCourseUrl2" :key="option.id"> {{ option.description }}</p>
      </div>
    </div>

    <div class="learningGroup">
      <div id="learningOutcomes">
       <div class="uiLabel" v-for="option in filteredCourseUrl1" :key="option.url"><a v-bind:href="option.url">{{ option.university}} <br> {{ option.title }}</a> <br> Learning Outcomes:</div>
       <p v-for="option in filteredCourseUrl1" :key="option.id" > {{ option.learningOutcomes }} </p>
      </div>
      
     <div id="learningOutcomes">
       <div class="uiLabel" v-for="option in filteredCourseUrl2" :key="option.url"><a v-bind:href="option.url">{{ option.university}} <br> {{ option.title }}</a> <br> Learning Outcomes:</div>
       <p v-for="option in filteredCourseUrl2" :key="option.id"> {{ option.learningOutcomes }} </p>
      </div>
    </div>

</body>
</template>
 
<script>
import { GChart } from "vue-google-charts";
import { getAPI } from '../axios-api'

export default {
  title: 'Course Information Discoverer',
  name: "App",
  components: {
    GChart
  },
  data() {
    return {
      /* chart arrays */
      collectionData: [
        ["Day", "Course1", "Course2"],
        [1,  7950, 14500],
        [2,  7950, 14500],
        [3,  7950, 14500],
        [4,  0, 14500],
        [5,  0 , 0],
      ],
      chartDataHeader: ['Year', 'Sales', 'Expenses', 'Profit'],
      chartDataRows: [
        ['2014', 1000, 400, 200],
        ['2015', 1170, 460, 250],
        ['2016', 660, 1120, 300],
        ['2017', 1030, 540, 350]
      ],
      updatedChartData: [],
      /*Array to store api data */
      APIData: [],
      /*declare variables to store v-models*/
      uniState1: null,
      uniState2: null,
      university1: null,
      university2: null,
      course1: null,
      course2: null,
      options: {
        chart: {
          title: "Fees to be paid over years"
        },
        hAxis: {title: 'Number of Years'},
        vAxis: {title: 'Yearly Fees in $'},
        width: 800,
        height: 400
      },
      columnOptions: {
        chart: {
          title: "Company Performance"
        },
        hAxis: {title: 'Total Fees in $'},
      },
    };
  },
  methods: {
    onCourseChange1() {
      this.updatedChartData = this.chartDataRows
        .map(row => {
          return row.map((item, index) => {
            if (index !== 0) {
              const max = item + 1000
              const min = 0
              return Math.floor(Math.random() * (max - min)) + min
            }
            return item
          })
        })
      }
  },
  computed: {
    universityStates () {
      return [...new Set(this.APIData.map(({ state }) => state))] /* returns array of unique states from API Data */
    },
    filteredUnis1() {
      var filtered = this.APIData.filter(option => !option.state.indexOf(this.uniState1)) /*returns filtered list of course objects depending on state chosen */
      return [...new Set(filtered.map(({ university }) => university))]  /*gets filtered list of course objects and returns a set without duplicate universities */
    },
    filteredUnis2() {
      var filtered = this.APIData.filter(option => !option.state.indexOf(this.uniState2)) 
      return [...new Set(filtered.map(({ university }) => university))]
    },
    filteredCourses1() {
      return this.APIData.filter(option => !option.university.indexOf(this.university1)) /*returns filtered list of course objects depending on uni chosen*/
    },
    filteredCourses2() {
      return this.APIData.filter(option => !option.university.indexOf(this.university2)) 
    },
    filteredCourseUrl1() {
      return this.APIData.filter(option => !option.url.indexOf(this.course1)) /*returns filtered list of course objects depending on the url of course chosen(used url instead of title because url is unique), the objects contain all fields/properties(can use in charts)*/
    },
    filteredCourseUrl2() {
      return this.APIData.filter(option => !option.url.indexOf(this.course2)) 
    },
    chartData () {
      return [ this.chartDataHeader, ...this.updatedChartData]
    }
  },
  created () {
    this.updateData(),
      getAPI.get('/api/courses/',) /* get request from our django restful api and log if its successful to console */
         .then(response => {
           console.log('Successfully retrieved data from API')
           this.APIData = response.data
         })
         .catch(err => { /* log failure if timeout after 5000 ms */
           console.log(err)
         })
  }
};
</script>


